import uniCard from "@/components/uni-card/uni-card.vue";
import api from "@/common/index.js";
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import over from '@/components/over.vue';
import imglazy from '@/components/img-lazy/img-lazy.vue';
let mvc = {
	detailList: [{
		item_name: '',
		item_en: '',
		item_no: '',
		price: '',
		QTY: '',
		sub_amt: ''
	}],
	orderInfo: {
		NUM: 0,
		sheet_amt: 0
	},
	sn: '',
	over: false,
	statusList: []
}
export default {
	components: {
		uniCard,
		uniNavBar,
		over,
		imglazy
	},
	data() {
		return {
			imglazyStyle:'border-radius: 10upx;',
			mvc: {
				detailList: [{
					item_name: '',
					item_en: '',
					item_no: '',
					price: '',
					QTY: '',
					sub_amt: ''
				}],
				orderInfo: {
					NUM: 0,
					sheet_amt: 0
				},
				sn: '',
				over: false,
				statusList: []
			}
		};
	},
	onLoad(option) {
		let that = this;
		setTimeout(function() {
			api.load(that.$t('index').loadmsg)
			mvc.sn = option.sn;
			that.loadData();
		}, 300)

	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		loadData() {
			let that = this;
			mvc.over = false;
			let params = {
				sn: mvc.sn
			}
			that.$ajax.orderHdView(params).then(res => {
				mvc.detailList = res.list;
				mvc.orderInfo = res.info;
				mvc.statusList = res.track;
				for (let i in mvc.detailList) {
					mvc.detailList[i].url = that.$store.getters.imgShow + "/thumb/" + mvc.detailList[i].item_no +
						".jpg?x-oss-process=style/300";
				}
				mvc.over = true;
				uni.hideLoading();
				that.mvc = mvc;
			})

		},
		//详情页
		navToDetailPage(item) {
			console.log(item)
			uni.navigateTo({
				url: `/pages/product/product?productId=${item}`
			})
		},
	},

}
